#preloader {
  overflow: hidden;
  background-color: #211b31;
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 999999; }

.mosh-preloader {
  overflow: hidden;
  -webkit-animation: 3000ms linear 0s normal none infinite running mosh-preloader;
  animation: 3000ms linear 0s normal none infinite running mosh-preloader;
  background-color: transparent;
  border-color: #f1f2f3 #f1f2f3 #4a7aec;
  border-radius: 50%;
  border-style: solid;
  border-width: 4px;
  height: 50px;
  left: calc(50% - 25px);
  position: relative;
  top: calc(50% - 25px);
  width: 50px;
  z-index: 9; }

@-webkit-keyframes mosh-preloader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes mosh-preloader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

